body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  background: #ecf0f1; }

.Search {
  margin: 0;
  width: 60vw; }
  .Search .content {
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
    margin-top: 30vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center; }
    .Search .content .logo {
      width: 40%;
      cursor: pointer; }
    .Search .content .search-field {
      -webkit-transition: all 0.4s ease;
      transition: all 0.4s ease;
      border-radius: 20px;
      background: white;
      width: 100%;
      height: 60px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      margin-top: 40px;
      box-shadow: 0 0 3px #c92723; }
      .Search .content .search-field input {
        font-family: 'Manrope', sans-serif !important;
        font-weight: 400;
        font-size: 24pt;
        width: 90%;
        height: 70%;
        text-decoration: none;
        border: none;
        background: white;
        margin-left: 15px; }
      .Search .content .search-field .go-search {
        height: 100%;
        width: 7%;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer; }
        .Search .content .search-field .go-search img {
          -webkit-transition: all 0.4s ease;
          transition: all 0.4s ease;
          opacity: .7; }
        .Search .content .search-field .go-search img:hover {
          -webkit-transform: scale(1.3);
                  transform: scale(1.3); }
      .Search .content .search-field.active {
        box-shadow: 0 0 8px #c92723; }
        .Search .content .search-field.active .go-search img {
          opacity: 1; }
    .Search .content.on-top {
      margin-top: 40px;
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
      height: 62px; }
      .Search .content.on-top .logo {
        height: 100%;
        width: auto; }
      .Search .content.on-top .search-field {
        width: 90%;
        margin: 0; }
  .Search .result {
    -webkit-transition: all 0.6s ease;
    transition: all 0.6s ease;
    opacity: 0;
    display: none;
    margin-top: 62px; }
    .Search .result.on-top {
      display: flex;
      opacity: 1; }

.Card {
  margin-top: 20px;
  width: 100%;
  font-family: 'Manrope', sans-serif !important;
  font-weight: 400; }
  .Card .minicard {
    display: flex;
    justify-content: space-around;
    height: 614px; }
    .Card .minicard .left-side {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 56%;
      margin-top: 4%; }
      .Card .minicard .left-side .left-header {
        display: flex;
        margin-bottom: 4%;
        align-items: center;
        width: 100%; }
        .Card .minicard .left-side .left-header .profile-img {
          height: 80px;
          width: 80px;
          border-radius: 50%; }
        .Card .minicard .left-side .left-header .description {
          margin-left: 3%;
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          align-items: flex-start; }
          .Card .minicard .left-side .left-header .description .title {
            font-size: 20pt; }
          .Card .minicard .left-side .left-header .description p {
            margin: 0; }
      .Card .minicard .left-side .cat-holder {
        width: 100%;
        display: flex;
        justify-content: center; }
        .Card .minicard .left-side .cat-holder .cat-switcher {
          border-top-left-radius: 4px;
          border-top-right-radius: 4px;
          width: 30%;
          border-top: 2px solid #ecf0f1;
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center; }
          .Card .minicard .left-side .cat-holder .cat-switcher.active {
            border-top: 2px solid #c92723;
            background: white; }
      .Card .minicard .left-side .cat-content {
        box-shadow: 0 0px 4px rgba(40, 40, 40, 0.15);
        border-radius: 4px;
        background: white;
        height: 400px;
        width: 100%; }
        .Card .minicard .left-side .cat-content .inf {
          height: 90%;
          width: 90%;
          margin: 5%;
          display: flex;
          flex-direction: column;
          justify-content: space-around; }
          .Card .minicard .left-side .cat-content .inf .wrapper {
            display: flex; }
            .Card .minicard .left-side .cat-content .inf .wrapper .right {
              margin-left: 10px;
              width: 40%;
              font-size: 12pt;
              font-weight: 600; }
            .Card .minicard .left-side .cat-content .inf .wrapper .left {
              margin-left: 20px;
              width: 50%;
              font-size: 12pt; }
          .Card .minicard .left-side .cat-content .inf.main {
            display: none; }
          .Card .minicard .left-side .cat-content .inf.dop {
            display: none; }
          .Card .minicard .left-side .cat-content .inf.active {
            display: flex; }
    .Card .minicard .right-side {
      margin-top: 4%;
      width: 36%;
      display: flex;
      flex-direction: column;
      align-items: center;
      overflow: scroll; }
      .Card .minicard .right-side .title {
        font-size: 14pt; }
    .Card .minicard .arrow-holder {
      height: 30%;
      width: 5%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 2%;
      cursor: pointer; }
      .Card .minicard .arrow-holder::after {
        -webkit-transition: all 0.8s ease;
        transition: all 0.8s ease;
        content: "";
        height: 20px;
        width: 20px;
        border-left: 3px solid #c92723;
        border-bottom: 3px solid #c92723;
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg); }
      .Card .minicard .arrow-holder.active::after {
        -webkit-transform: rotate(135deg);
                transform: rotate(135deg); }
  .Card .all-info {
    -webkit-transition: all .8s ease;
    transition: all .8s ease;
    width: 100%;
    height: 0px;
    overflow: hidden; }
    .Card .all-info .info {
      width: 100%;
      height: 300px; }
      .Card .all-info .info .title {
        font-size: 25pt;
        font-weight: 300;
        color: #c92723; }
        .Card .all-info .info .title p {
          margin-left: 10px; }
    .Card .all-info.active {
      height: 600px; }

